import React, {useEffect, useState} from 'react'
import Footer from "../components/UI/footer/Footer";
import Header from '../components/UI/header/Header';
import '../styles/style.css';
import AnalyzesListContainer from "../components/AnalyzesListContainer";
import Modal from "../components/modal/Modal";
import CartModal from "../components/cartModal/CartModal";
import LoginModal from "../components/loginModal/LoginModal";
import FilterContainer from "../components/filterContainer/FilterContainer";


function MainPage() {

	const analyzesDisplayModeArray = ['byCategories', 'popularAnalyzes', 'allAnalyzes', 'searchedAnalyzes'];

	const [isModalActive, setIsModalActive] = useState(false);
	const [currentCategory, setCurrentCategory] = useState({categoryid: 1, categoryname: 'Аллергены ImmunoCAP'});
	const [analyzesDisplayMode, setAnalyzesDisplayMode] = useState(analyzesDisplayModeArray[1]);
	const [searchQuery, setSearchQuery] = useState('');
	const [analyzesInCart, setAnalyzesInCart] = useState([]);
	const [isCartModalActive, setIsCartModalActive] = useState(false);
	const [isLoginModalActive, setIsLoginModalActive] = useState(false);
	const [isLoaded, setIsLoaded] = useState(false);

	function addItemInCart(newItem, showError) {
		let isInclude = false;
		for (let item of analyzesInCart) {
			if (item.analysisid === newItem.analysisid)
				isInclude = true;
		}
		isInclude ? showError() : setAnalyzesInCart([...analyzesInCart,newItem]);
	}

	function onPopularClick() {
		setAnalyzesDisplayMode(analyzesDisplayModeArray[1]);
	}

	function onAllClick() {
		setAnalyzesDisplayMode(analyzesDisplayModeArray[2]);
	}

	useEffect( () => {
		if (isLoaded)
			sessionStorage.setItem('analyzesInCart', JSON.stringify(analyzesInCart));
	}, [analyzesInCart])

	useEffect(() => {
		if (isLoaded === false) {
			const storageCart = JSON.parse(sessionStorage.getItem('analyzesInCart'));
			if (storageCart !== null)
				setAnalyzesInCart(storageCart);
			setIsLoaded(true);
		}
	}, [isLoaded]);

	const inputProps = {
		searchQuery: searchQuery,
		setSearchQuery: setSearchQuery
	}

	return (
		<div className="MainPage" style={{position: "relative"}}>
			<Modal isActive={isModalActive} setIsActive={setIsModalActive} setCurrentCategory={setCurrentCategory}
			       setAnalyzesDisplayMode={setAnalyzesDisplayMode}
			       analyzesDisplayModeArray={analyzesDisplayModeArray}></Modal>
			<CartModal analyzesInCart={analyzesInCart} isActive={isCartModalActive} setAnalyzesInCart={setAnalyzesInCart}></CartModal>
			<LoginModal isActive={isLoginModalActive} setIsActive={setIsLoginModalActive}></LoginModal>
			<div className="content">
			<Header setIsCartActive={setIsCartModalActive}
			        isCartActive={isCartModalActive}
			        setIsLoginModalActive={setIsLoginModalActive}>
			</Header>
			<FilterContainer setIsModalActive={setIsModalActive} onAllClick={onAllClick} onPopularClick={onPopularClick} inputProps={inputProps}></FilterContainer>
			<div className="analysis-container" style={{display: 'flex', justifyContent: 'center', marginTop: '40px'}}>
				<AnalyzesListContainer category={currentCategory} analyzesDisplayMode={analyzesDisplayMode}
				                       analyzesDisplayModeArray={analyzesDisplayModeArray}
				                       searchQuery={searchQuery}
				                       setAnalyzesDisplayMode={setAnalyzesDisplayMode}
				                       addInCart={addItemInCart}>
				</AnalyzesListContainer>
			</div>
			</div>
			<Footer></Footer>
		</div>
	);
}

export default MainPage;