import React, {useEffect, useState} from 'react';
import classes from "./AnalyzesResults.module.css";
import AnalyzesResultsRow from "./AnalyzesResultsRow";

const AnalyzesResults = ({client}) => {

	const [results, setResults] = useState([]);

	async function getResults() {
		const url = "http://localhost:5000/api/AnalysisResults";
		const token = sessionStorage.getItem('token');
		const response = await fetch(url, {
			headers: {
				"Authorization": "Bearer " + token
			}
		})
		const json = await response.json();
		console.log(json);
		setResults(json);
	}

	useEffect(() => {
		getResults();
	}, [])

	function resultsRender() {
		if (results.length === 0)
			return <div className={classes.empty}>Пока вы не сделали ни одного заказа</div>
		return (
			results.map((item) =>
				<AnalyzesResultsRow key={item.id} result={item}></AnalyzesResultsRow>
			)
		);
	}

	return (
		<div className={classes.analyzesResults}>
			<div className={`${classes.tableRow} ${classes.header}`}>
				<div className={`${classes.column}`}>Дата</div>
				<div className={`${classes.column}`}>Название</div>
				<div className={`${classes.column}`}>Статус</div>
				<div className={`${classes.column}`}>Срок выполнения</div>
			</div>
			{resultsRender()}
		</div>
	);
};

export default AnalyzesResults;