import React, {useEffect, useState} from 'react';
import InputField from "../UI/inputField/InputField";
import classes from "./clientData.module.css";
import MyButton from "../UI/button/MyButton";

const ClientData = ({client}) => {

	const [name, setName] = useState('');
	const [phoneNumber, setPhoneNumber] = useState('');
	const [email, setEmail] = useState('');
	const [birthDate, setBirthDate] = useState('');
	const [password, setPassword] = useState('');



	useEffect(() => {
		setName(client.name);
		setPhoneNumber(client.phoneNumber);
		setEmail(client.email);
		setBirthDate(client.birthDate);
		setPassword(client.password);
	}, [client])


	async function postClient() {
		const token = sessionStorage.getItem('token');
		const url = `http://localhost:5000/api/Client/${client.id}`;
		const editedClient = {
			clientid: client.id,
			Clientname: name,
			Phonenumber: phoneNumber,
			Passwordhash: password,
			Email: email,
			BirthDate: birthDate
		};
		const response = await fetch(url, {
			method: 'PUT',
			body: JSON.stringify(editedClient),
			headers: {
				'Content-Type': 'application/json',
				"Authorization": "Bearer " + token
			}
		});
		console.log(response)
	}

	return (
		<div className='clientData'>
			<div className={classes.title}>Личные данные</div>
			<div className={classes.inputFieldsWrapper}>
				<InputField title={'Имя клиента'} value={name} setValue={setName}></InputField>
				<InputField title={'Номер телефона'} value={phoneNumber} setValue={setPhoneNumber}></InputField>
				<InputField title={'Email'} value={email} setValue={setEmail}></InputField>
				<InputField title={'Дата рождения'} value={birthDate} setValue={setBirthDate} type={'date'}></InputField>
				<InputField title={'Пароль'} value={password} setValue={setPassword} type={'password'}></InputField>
				<div className={classes.buttonWrapper}>
					<MyButton style={{backgroundColor: '#a29c9c'}}>Отменить</MyButton>
					<MyButton onClick={() => postClient()}>Сохранить изменения</MyButton>
				</div>
			</div>

		</div>
	);
};

export default ClientData;