import React, {useState} from 'react';
import classes from "./AnalysisBlock.module.css";
import MyButton from "../UI/button/MyButton";

const AnalysisBlock = ({data, addInCart}) => {

	const [errorState, setErrorState] = useState(`${classes.error__hidden}`);
	const buttonStyle = {
		width: '120px',
		height: '30px',
		position: 'absolute',
		bottom: '10%',
		right: '30px'
	}

	function showError() {
		setErrorState(`${classes.error}`);
		setTimeout(() => setErrorState(`${classes.error__hidden}`), 2000);
	}

	return (
		<div className={classes.analysisBlock}>
			<div className={classes.title}>{data.analysistitle}</div>
			<div className={classes.duration}>{data.duration}</div>
			<div className={classes.price}>{data.price}₽</div>
			<MyButton style={buttonStyle} onClick={() => addInCart(data, showError)}>В корзину</MyButton>
			<div className={errorState}>Уже в корзине!</div>
		</div>
	);
};

export default AnalysisBlock;