import React, {useEffect, useState} from 'react';
import Header from "../components/UI/header/Header";
import Footer from "../components/UI/footer/Footer";
import '../styles/style.css';
import {Navigate, Route, Routes} from "react-router-dom";
import ProfileNav from "../components/ProfileNav/ProfileNav";
import ClientData from "../components/clientData/ClientData";
import CartModal from "../components/cartModal/CartModal";
import AnalyzesResults from "../components/analyzesResults/AnalyzesResults";

const Profile = () => {

	const viewModeList = {
		orders: 1,
		results: 2,
		personal: 3
	}

	const [isCartModalActive, setIsCartModalActive] = useState(false);
	const [analyzesInCart, setAnalyzesInCart] = useState([]);
	const [isLoaded, setIsLoaded] = useState(false);

	function Client(id, name, phoneNumber, email, password, birthDate) {
		this.id = id;
		this.name = name;
		this.phoneNumber = phoneNumber;
		this.email = email;
		this.password = password;
		this.birthDate = birthDate;
	}

	const [client, setClient] = useState({});

	async function getClientInfo() {
		const token = sessionStorage.getItem('token');
		const url = 'http://localhost:5000/api/Client';
		const response = await fetch(url, {
			headers: {
				"Authorization": "Bearer " + token
			}
		})
		const json = await response.json();
		setClient(new Client(json.Clientid, json.Clientname, json.Phonenumber, json.Email, json.Passwordhash, json.BirthDate));
	}

	useEffect( () => {
		if (isLoaded)
			sessionStorage.setItem('analyzesInCart', JSON.stringify(analyzesInCart));
	}, [analyzesInCart])

	useEffect(() => {
		if (isLoaded === false) {
			getClientInfo();
			const storageCart = JSON.parse(sessionStorage.getItem('analyzesInCart'));
			if (storageCart !== null)
				setAnalyzesInCart(storageCart);
			setIsLoaded(true);
		}
	}, [isLoaded]);


	const [viewMode, setViewMode] = useState(viewModeList.results)
	return (
		<div className='profile'>
			<CartModal analyzesInCart={analyzesInCart} isActive={isCartModalActive} setAnalyzesInCart={setAnalyzesInCart}></CartModal>
			<div className="content">
				<Header viewMode={'profile'} isCartActive={isCartModalActive} setIsCartActive={setIsCartModalActive}></Header>
				<ProfileNav mode={viewMode} setMode={setViewMode} modeList={viewModeList}></ProfileNav>
				<div className="container" style={{width: '1200px'}}>
					<div className="clientName" style={{fontSize: '28px', padding: '30px 0'}}>{client.name}</div>
					<Routes>
						<Route path={'/results'} element={<AnalyzesResults client={client}></AnalyzesResults>}/>
						<Route path={'/personal'} element={<ClientData client={client}></ClientData>}/>
						<Route
							path="*"
							element={<Navigate to="/profile/results" replace />}
						/>
					</Routes>
				</div>
			</div>
			<Footer></Footer>
		</div>
	);
};

export default Profile;