import React from 'react';
import classes from "./CartModal.module.css";
import ItemInCart from "./ItemInCart";
import MyButton from "../UI/button/MyButton";
import AuthorizedFetch from "../../API/AuthorizedFetch";

const CartModal = ({analyzesInCart, isActive, setAnalyzesInCart}) => {

	function onDelete(removedItem) {
		setAnalyzesInCart(analyzesInCart.filter(item => item.analysisid !== removedItem.analysisid));
	}

	function checkIsActive() {
		if (analyzesInCart.length === 0 || isActive === false)
			return `${classes.modalCart}`;
		return `${classes.active} ${classes.modalCart}`;
	}

	function getAmountPrice(analyzes) {
		let sum = 0;
		for (let item of analyzes) {
			sum += item.price;
		}
		return sum;
	}

	async function makePurchase() {
		const analyzesId = analyzesInCart.map(t => t.analysisid);
		const token = sessionStorage.getItem('token');
		const url = "http://localhost:5000/api/AnalysisResults";

		await fetch(url, {
			method: 'POST',
			headers: {
				"Authorization": "Bearer " + token,
				"Content-Type": "application/json",
			},
			body: JSON.stringify(analyzesId),
		})
	}

	return (
		<div className={checkIsActive()}>
			<div className={classes.list}>
				{analyzesInCart.map(analysis =>
					<ItemInCart key={analysis.analysisid} item={analysis} onDelete={onDelete}></ItemInCart>)}
			</div>
			<div className={classes.bottomWrapper}>
				<div className={classes.amount}>Итого: {getAmountPrice(analyzesInCart)}₽</div>
				<MyButton style={{width:'180px', height: '35px'}} onClick={makePurchase}>Оформить заказ</MyButton>
			</div>

		</div>
	);
};

export default CartModal;